import React from 'react';
import { Row, Col, Card, Form, Button, Collapse, InputGroup, FormControl, DropdownButton, Dropdown, Table } from 'react-bootstrap';

import Aux from "../../hoc/_Aux";

import DatePicker from 'react-datepicker';
import AsyncSelect from 'react-select/async';
import BootstrapSwitchButton from 'bootstrap-switch-button-react'


import ErrorBody from '../../App/components/Reusables/ErrorBody';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import Loader from '../../App/components/Reusables/Loader'
import { createAccount, getAccountTypes, getAccounts } from '../../api/accounting/account';
import { getLedgerEntryTypes, createLedgerEntry } from '../../api/accounting/ledger';
import { validateLedgerEntryCreation } from '../../validation/ledger';
import isEmpty from '../../validation/isEmpty';
import { getPaymentMethods, createPayment } from '../../api/payment/payment';
import { validatePaymentCreation } from '../../validation/payment';
import styled from "styled-components";
import store from '../../redux/store';
import { RESET_FETCH_MORE_OPTIONS, SET_FETCH_MORE_OPTIONS, SET_IS_MENU_OPEN } from '../../redux/actions/types';
import DEMO from '../../globals/constant';
import CustomSelectMenuList from '../../App/components/Reusables/CustomSelectMenuList';


const IconButtonWrapper = styled.div`
  float: right;
  transform: rotate(0deg);
  overflow: hidden;
  transition: all 0.3s ease-out;
  transform: ${props => (props.rotate ? `rotate(90deg)` : "")};
`;


const ledgerEntryModel = {
    invoice: null,
    purchaseOrder: null,
    amount: '', //payment amount
    method: '', //payment method
    methodHeader: '',
    transactionFrom: '',
    bankName: '',
    transactionId: '',
    paymentRemarks: '',

    accountId: '',
    accountName: '',
    drAmount: '',
    crAmount: '',
    entryRemarks: '',

    showMore: false,
}
const Create = (props) => {

    const [loading, setLoading] = React.useState(true);
    const [submitting, setSubmitting] = React.useState(false);
    const [ledgerEntryTypes, setLedgerEntryTypes] = React.useState([]);
    const [paymentMethods, setPaymentMethods] = React.useState([])
    const [entryDate, setEntryDate] = React.useState(new Date())
    const [ledgerEntriesData, setLedgerEntriesData] = React.useState([{ ...ledgerEntryModel }, { ...ledgerEntryModel, isAdd: false }])
    const [dateError, setDateError] = React.useState('')
    const [entryErrors, setEntryErrors] = React.useState([]);


    const [accountSearchMode, setAccountSearchMode] = React.useState('static');
    const [accountInputText, setAccountInputText] = React.useState('');
    const [defaultAccountOptions, setDefaultAccountOptions] = React.useState([]);
    const [accountOptionsMaxLength, setAccountOptionsMaxLength] = React.useState(0);


    //custom API call
    const getAccounts_ = async (signal) => {
        getAccounts({
            "action": 'get',
            "filter": 'none',
            "start": defaultAccountOptions.length,
            "end": defaultAccountOptions.length + DEMO.INSTANCE_PER_LOAD
        }, signal)
            .then((res) => {
                if (res.json.status) {
                    setDefaultAccountOptions([
                        ...defaultAccountOptions,
                        ...res.json.accounts.map((itm, index) => ({ label: `${itm.name}`, value: itm.id }))
                    ]);
                    setAccountOptionsMaxLength(res.json.count)
                    store.dispatch({
                        type: SET_FETCH_MORE_OPTIONS,
                        payload: ['fetchMoreOptionsAccs', false]
                    })
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            }).catch((err) => {
                console.log(err)
            });
    }


    React.useEffect(() => {
        const controller = new AbortController();
        (async () => {
            let ladder = 0;
            await getPaymentMethods({ action: 'get' }, controller.signal)
                .then(res => {
                    console.log(res);
                    if (res.json.status) {
                        setPaymentMethods([...res.json.payment_methods])
                        ladder++;
                    } else {
                        Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                    }
                })
                .catch(err => {
                    console.log(err);
                })
            if (ladder === 1) {
                setLoading(false)
            }
        })()
        getAccounts_(controller.signal)
        return () => controller.abort();
    }, []);


    const loadAccountOptions = async (inputText) => {
        const requestJson = { action: 'get', filter: 'name', name: inputText, start: 0, end: 50 }
        return getAccounts(requestJson)
            .then((res) => {
                console.log(res);
                if (res.json.status) {
                    return res.json.accounts.map((itm, index) => ({ label: `${itm.name}`, value: itm.id, uuid: itm.uuid }));
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch((err) => {
                console.log(err)
            });
    }

    const amendErrorOf = (row, field) => {
        let tempEntryErrors = entryErrors;
        if (tempEntryErrors[row]) {
            tempEntryErrors[row][field] = '';
            setEntryErrors([...tempEntryErrors])
        }
    }

    const _addLedgerEntry = (e) => {
        e.preventDefault();
        setLedgerEntriesData([...ledgerEntriesData, { ...ledgerEntryModel }])
    }

    const _removeLedgerEntry = (removedEntryIndex) => {

        //remove row selected
        const tempEntriesData = ledgerEntriesData;
        tempEntriesData.splice(removedEntryIndex, 1);
        setLedgerEntriesData([...tempEntriesData]);

        //remove errors of row selected
        const tempEntryErrors = [...entryErrors]
        tempEntryErrors.splice(removedEntryIndex, 1)
        setEntryErrors(tempEntryErrors)
    }

    const _entryDateChange = (selectedDate) => {
        setDateError('')
        setEntryDate(selectedDate)
    }

    const _showMore = (row) => {
        const tempEntriesData = [...ledgerEntriesData]
        tempEntriesData[row] = {
            ...tempEntriesData[row],
            showMore: !tempEntriesData[row].showMore
        }
        setLedgerEntriesData(tempEntriesData);
    }

    const _accountInputChange = (inputText) => {
        setAccountInputText(inputText);
    }

    const _scrollAccountMenuToBottom = (e) => {
        e.preventDefault();
        if (accountOptionsMaxLength > defaultAccountOptions.length && !accountInputText) {
            store.dispatch({
                type: SET_FETCH_MORE_OPTIONS,
                payload: ['fetchMoreOptionsAccs', true]
            })
            getAccounts_();
        }
    }

    const _selectedAccountChange = (row, account) => {
        amendErrorOf(row, 'accountId');
        const tempLedgerEntriesData = ledgerEntriesData;
        tempLedgerEntriesData[row].accountId = account.value;
        tempLedgerEntriesData[row].accountName = account.label;
        setLedgerEntriesData([...tempLedgerEntriesData])
    }

    const _ledgerEntriesDataChange = (row, e) => {
        console.log(row, e.target.value);
        const type = e.target.type;
        const name = e.target.name;
        const value = e.target.value;
        amendErrorOf(row, name)
        const tempLedgerEntriesData = ledgerEntriesData;
        tempLedgerEntriesData[row][name] = type === 'number' ? parseFloat(value) : value
        if (name === 'method') {
            const typeData = paymentMethods.find(type => type.id == e.target.value);
            tempLedgerEntriesData[row]['methodHeader'] = typeData.header;
        }
        setLedgerEntriesData([...tempLedgerEntriesData])
    }


    const _submit = async () => {
        //check if there are errors
        console.log('inside submit')

        if (!(entryDate ? entryDate.toISOString() : null)) {
            setDateError('Date should be provided!');
            return;
        }
        const entriesValidity = validateLedgerEntryCreation(ledgerEntriesData);
        if (!entriesValidity.isValid) {
            console.log(entriesValidity.errors);
            setEntryErrors([...entriesValidity.errors])
            return;
        };

        // Check if the Debit amount and Credit amount are equal or not
        // let drAmt = 0;
        // let crAmt = 0;
        // for (let anEntry of ledgerEntriesData) {
        //     //get the entry type to know if it is for Dr or Cr
        //     console.log('calculating debit and credit');
        //     if (anEntry.drAmount) {
        //         drAmt += anEntry.drAmount;
        //     } else {
        //         crAmt += anEntry.crAmount
        //     }
        // }
        // console.log(drAmt, crAmt);
        // if (drAmt !== crAmt) {
        //     Jhilikka.fire({ icon: 'error', title: 'Debit and Credit amount is not equal!', background: '#ffe5de' })
        //     return;
        // }

        console.log('no errros');


        setSubmitting(true);
        const paymentsJson = {
            "action": "add",
            "payments": [],
        }

        const entriesJson = {
            "action": "add",
            "bundle_id": null,
            "entries": []
        }
        for (let anEntry of ledgerEntriesData) {
            paymentsJson['payments'].push({
                "invoice": anEntry.invoice,
                "purchase_order": anEntry.purchaseOrder,
                "amount": parseFloat(anEntry.drAmount || anEntry.crAmount),
                "method": parseInt(anEntry.method),
                "bank_name": anEntry.bankName,
                "transaction_from": anEntry.transactionFrom,
                "transaction_id": anEntry.transactionId,
                "remarks": anEntry.paymentRemarks,
            })
        }
        console.log(JSON.stringify(paymentsJson), 'is the data to be submitted');
        console.log('-------------------------------------------------------------')
        console.log('-------------------------------------------------------------')
        console.log('-------------------------------------------------------------')






        console.log('-------------------------------------------------------------')
        console.log('-------------------------------------------------------------')
        console.log('-------------------------------------------------------------')
        console.log('-------------------------------------------------------------')

        try {
            const res1 = await createPayment(paymentsJson)
            console.log(res1)
            if (res1.json.status) {
                console.log('payment has be created ************************')
                for (let anEntry of ledgerEntriesData) {
                    entriesJson['entries'].push({
                        "account_id": parseInt(anEntry.accountId),
                        "is_add": anEntry.drAmount ? true : false,
                        "date": entryDate,
                        "payment_id": res1.json.payments[ledgerEntriesData.indexOf(anEntry)].id,
                        "remarks": anEntry.entryRemarks,

                    })
                }

                console.log(JSON.stringify(entriesJson), 'is the data to be submitted');
                const res2 = await createLedgerEntry(entriesJson);
                console.log(res2)
                if (res2.json.status) {
                    Jhilikka.fire({ icon: 'success', title: 'Ledger Entry created successfully!', background: '#dffff0' })
                    props.history.push('/ledger-entries');
                } else {
                    setSubmitting(false);
                    Jhilikka.fire({ icon: 'error', title: res2.json.error, background: '#ffe5de' })
                }


            } else { Jhilikka.fire({ icon: 'error', title: res1.json.error, background: '#ffe5de' }) }

        } catch (err) {
            setSubmitting(false)
            console.log(err)
        };

    }

    const LedgerEntriesList = ledgerEntriesData.map((entry, index) => {
        return ([

            <div className="ledgerRow" key={index} style={{ display: 'flex', }} >
                <span  >
                    <div
                        onClick={() => _removeLedgerEntry(index)}
                        style={{ cursor: 'pointer', margin: '10px 0', border: '1px solid #f44236', borderRadius: '3px', width: '25px', textAlign: 'center' }}
                    >
                        <i className="feather icon-trash text-c-red f-15 m-0" />
                    </div>
                </span>
                <span style={{ marginLeft: '4px' }} >
                    <div
                        onClick={() => _showMore(index)}
                        style={{
                            cursor: 'pointer',
                            margin: '10px 0',
                            border: '1px solid #007bff',
                            borderRadius: '3px',
                            width: '50px',
                            textAlign: 'center',
                            fontSize: '0.93em',
                            color: ledgerEntriesData[index].showMore ? 'white' : '#007bff',
                            backgroundColor: ledgerEntriesData[index].showMore ? '#007bff' : 'white',

                        }}
                    >
                        More
                    </div>
                </span>

                <div style={{ minWidth: '180px' }}>
                    <AsyncSelect
                        defaultOptions={defaultAccountOptions}
                        value={ledgerEntriesData[index].accountId ? { label: ledgerEntriesData[index].accountName, value: ledgerEntriesData[index].accountId } : null}
                        onChange={(account) => _selectedAccountChange(index, account)}
                        placeholder='Type account name'
                        loadOptions={loadAccountOptions}
                        onInputChange={_accountInputChange}
                        components={{
                            MenuList: CustomSelectMenuList,
                        }}
                        onMenuScrollToBottom={_scrollAccountMenuToBottom}
                        onMenuClose={() => {
                            store.dispatch({ type: RESET_FETCH_MORE_OPTIONS })
                            store.dispatch({ type: SET_IS_MENU_OPEN, payload: ['acc', false] })
                        }}
                        onMenuOpen={() => {
                            store.dispatch({ type: SET_IS_MENU_OPEN, payload: ['acc', true] })
                        }}
                    />
                    <ErrorBody>{entryErrors[index] ? entryErrors[index].accountId : null}</ErrorBody>
                </div>



                <div>
                    <Form.Control
                        name="drAmount"
                        disabled={ledgerEntriesData[index].crAmount ? true : false}
                        value={ledgerEntriesData[index].drAmount || ''}
                        onChange={(e) => _ledgerEntriesDataChange(index, e)}
                        type="number"
                        placeholder="Amount"
                    />
                    <ErrorBody>{entryErrors[index] ? entryErrors[index].drAmount : null}</ErrorBody>
                </div>
                <div>
                    <Form.Control
                        name="crAmount"
                        disabled={ledgerEntriesData[index].drAmount ? true : false}
                        value={ledgerEntriesData[index].crAmount || ''}
                        onChange={(e) => _ledgerEntriesDataChange(index, e)}
                        type="number"
                        placeholder="Amount"
                    />
                    <ErrorBody>{entryErrors[index] ? entryErrors[index].crAmount : null}</ErrorBody>
                </div>

                <div >
                    <Form.Control
                        name="entryRemarks"
                        as="textarea"
                        rows="1"
                        onChange={(e) => _ledgerEntriesDataChange(index, e)}
                        placeholder="Narration"
                    />
                    <ErrorBody>{entryErrors[index] ? entryErrors[index].entryRemarks : null}</ErrorBody>
                </div>
                <div style={{ minWidth: '160px' }}>
                    <Form.Control
                        name="method"
                        value={ledgerEntriesData[index].method}
                        onChange={(e) => _ledgerEntriesDataChange(index, e)}
                        type={'number'}
                        as="select"
                    >
                        <option value='' disabled >Select...</option>
                        {paymentMethods.map((method, index) => {
                            return <option key={index} value={method.id}>{method.name}</option>
                        })}
                    </Form.Control>
                    <ErrorBody>{entryErrors[index] ? entryErrors[index].method : null}</ErrorBody>
                </div>



            </div >,

            <Collapse key={index + 'key'} in={ledgerEntriesData[index].showMore}>
                <div>
                    < div style={{ borderTop: "none !important", display: 'flex', marginTop: '12px' }} className="ledgerRow secondInputRow">

                        <span style={{ borderTop: 'none !important' }} >
                            <div
                                style={{ cursor: 'pointer', margin: '10px 0', border: '0 solid #f44236', borderRadius: '3px', width: '25px', textAlign: 'center' }}
                            >
                                <i className="feather icon text-c-red f-15 m-0" />
                            </div>
                        </span>
                        <span style={{ borderTop: 'none !important', marginLeft: '4px' }} >
                            <div
                                style={{ cursor: 'pointer', margin: '10px 0', border: '0 solid #f44236', borderRadius: '3px', width: '50px', textAlign: 'center' }}
                            >
                                <i className="feather icon text-c-red f-15 m-0" />
                            </div>
                        </span>

                        <div>
                            <Form.Label>Transaction From</Form.Label> <br />
                            <Form.Control
                                name="transactionFrom"
                                value={ledgerEntriesData[index].transactionFrom}
                                onChange={(e) => _ledgerEntriesDataChange(index, e)}
                                type="text"
                                placeholder="Transaction From"
                            />
                            <ErrorBody>{entryErrors[index] ? entryErrors[index].transactionFrom : null}</ErrorBody>
                        </div>

                        {ledgerEntriesData[index].methodHeader === 'bank' ?
                            <div>
                                <Form.Label>Bank Name</Form.Label> <br />
                                <Form.Control
                                    name="bankName"
                                    value={ledgerEntriesData[index].bankName}
                                    onChange={(e) => _ledgerEntriesDataChange(index, e)}
                                    type="text"
                                    placeholder="Bank Name"
                                />
                                <ErrorBody>{entryErrors[index] ? entryErrors[index].bankName : null}</ErrorBody>
                            </div> :
                            null}


                        <div>
                            <Form.Label>Transaction ID</Form.Label> <br />
                            <Form.Control
                                name="transactionId"
                                value={ledgerEntriesData[index].transactionId}
                                onChange={(e) => _ledgerEntriesDataChange(index, e)}
                                type="text"
                                placeholder="Transaction ID"
                            />
                            <ErrorBody>{entryErrors[index] ? entryErrors[index].transactionId : null}</ErrorBody>
                        </div>
                        <div >
                            <Form.Label>Payment Remarks</Form.Label> <br />
                            <Form.Control
                                name="paymentRemarks"
                                as="textarea"
                                rows="1"
                                onChange={(e) => _ledgerEntriesDataChange(index, e)}
                                placeholder="Remarks"
                            />
                            <ErrorBody>{entryErrors[index] ? entryErrors[index].paymentRemarks : null}</ErrorBody>
                        </div>
                    </div>
                </div>

            </Collapse>,

            <hr key={'sd' + index} />,
        ]


        )
    })

    console.log(ledgerEntriesData);


    if (loading) return <Loader />
    return (
        <Aux>
            <Row>
                <Col>
                    <Card>
                        <Card.Body>
                            <h5>Create Ledger Entry</h5>
                            <hr />
                            <Row>
                                <Col md={6}>

                                    <Form.Group  >
                                        <Form.Label>Date</Form.Label> <br />
                                        <DatePicker onChange={_entryDateChange} placeholder="Pick Date" name="entryDate" selected={entryDate} />
                                        <ErrorBody>{dateError}</ErrorBody>
                                    </Form.Group>
                                </Col>
                            </Row>



                            <Card.Header>
                                <Card.Title as="h5">Entries</Card.Title>
                            </Card.Header>
                            <div >
                                <div style={{ overflowX: 'scroll', overflow: 'auto', paddingBottom: '200px' }}>
                                    <div className="ledgerRow ledgerHeadingRow" style={{ display: 'flex', }} >
                                        <span style={{ borderTop: 'none !important' }} >
                                            <div
                                                style={{ margin: '10px 0', border: '0 solid #f44236', borderRadius: '3px', width: '25px', textAlign: 'center' }}
                                            >
                                                <i className="feather icon text-c-red f-15 m-0" />
                                            </div>
                                        </span>

                                        <span style={{ borderTop: 'none !important' }} >
                                            <div
                                                style={{ margin: '10px 0', border: '0 solid #f44236', borderRadius: '3px', width: '50px', textAlign: 'center' }}
                                            >
                                                <i className="feather icon text-c-red f-15 m-0" />
                                            </div>
                                        </span>
                                        <div style={{ minWidth: '180px' }}>
                                            <div><b>Account</b></div>
                                        </div>
                                        <div>
                                            <div><b>Debits</b></div>
                                        </div>
                                        <div>
                                            <div><b>Credits</b></div>
                                        </div>
                                        <div >
                                            <div><b>Narration</b></div>
                                        </div>
                                        <div style={{ minWidth: '160px' }}>
                                            <div><b>Payment Method</b></div>
                                        </div>
                                    </div >
                                    <hr style={{ marginTop: '0px' }} />

                                    {LedgerEntriesList}

                                    < div className="ledgerRow" style={{ display: 'flex', }} >
                                        <span scope="row" style={{ borderTop: 'none !important', marginTop: 0 }}>
                                            <div onClick={_addLedgerEntry} style={{ cursor: 'pointer', textAlign: 'center', border: '1px solid #1de9b6', borderRadius: '3px', width: '25px' }}>
                                                <i className="feather icon-plus text-c-green f-15" />
                                            </div>
                                        </span>
                                    </div>


                                </div>
                            </div>

                            <div className="d-flex justify-content-end"  >
                                <Button
                                    variant="warning"
                                    onClick={() => { props.history.push('/ledger-entries') }}
                                >
                                    Cancel
                                    </Button>
                                <Button
                                    disabled={submitting}
                                    variant="primary"
                                    onClick={_submit}
                                >
                                    {submitting ? <><i className="fa fa-spinner text-c-white f-15 fa-pulse m-l-1"></i>Submitting</> : 'Submit'}

                                </Button>
                            </div>

                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </Aux >
    );
}

export default Create;
